/*
 * Element: Link Social
 */
.social-link {
  position: relative;
  width: 40px;
  height: 40px;
  background-color: #acacac;
  border-radius: 50%;
}
.social-link:hover,
.social-link:focus {
  background: #939393;
}
.social-link__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 20px;
  fill: #4b4b4b;
}
.social-link--facebook {
  background-color: #336699;
}
.social-link--facebook .social-link__icon {
  fill: #ffffff;
}
